@font-face {
  font-family: 'Gill Sans';
  src: url('/src/fonts/GillSans Regular.ttf') format("truetype");
  font-weight: normal;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #F8F8F8;
  font-family: "Gill Sans", Calibri, sans-serif;
}
h1,
h2,
h3 {
  font-weight: bold;
}
h1 {
  font-size: 36px;
}
h2 {
  font-size: 24px;
}
h3 {
  font-size: 14px;
}
.container {
  max-width: 1094px;
  margin: 0 auto;
  padding: 20px 0;
}
.top-info {
  display: flex;
  justify-content: start;
  height: 80px;
}
.top-info > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0 0 25%;
  max-width: 25%;
  align-items: flex-start;
}
.top-info * img {
  width: 13.73px;
  margin-right: 10px;
}
.top-info .top-info__burger-wrapper {
  display: none;
}
.top-info__logo img {
  width: 215px;
}
.top-info__schedule {
  font-size: 16px;
  margin-top: 10px;
}
.top-info__schedule :last-child {
  font-size: 14px;
}
.top-info__contacts a {
  color: #000;
  font-size: 14px;
}
.top-info__contacts :first-child {
  font-size: 18px;
  font-weight: bold;
  font-family: 'Gill Sans', Calibri, sans-serif;
  text-decoration: none;
}
.top-info .top-info__call {
  align-items: center;
  max-width: 200px;
  margin-left: auto;
}
.top-info .top-info__call a {
  color: #000;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
}
.top-info .top-info__call button {
  background-color: #3157BE;
  color: #fff;
  width: 100%;
  height: 44px;
  border: none;
  border-radius: 100px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  transition: 0.3s;
  cursor: pointer;
}
.top-info .top-info__call button :hover {
  background-color: #274495;
}
@media (max-width: 1025px) {
  .top-info .top-info__contacts,
  .top-info .top-info__schedule {
    display: none;
  }
  .top-info .top-info__burger-wrapper {
    display: flex;
    align-items: center;
  }
  .top-info .top-info__burger-wrapper .lang {
    display: flex;
    align-items: center;
    position: relative;
  }
  .top-info .top-info__burger-wrapper .menu__item-extended {
    display: none;
  }
  .top-info .top-info__burger-wrapper .menu__item-extended-active {
    position: absolute;
    top: 25px;
    flex-direction: column;
    z-index: 5;
    background-color: #fff;
    padding: 10px;
    margin-left: -10px;
    border-radius: 15px;
    display: flex;
  }
  .top-info .top-info__burger-wrapper .menu__item-extended a {
    color: #000;
    text-decoration: none;
    margin: 5px 0;
  }
  .top-info .top-info__burger-wrapper .lang span {
    border-bottom: 2px solid #000;
    width: 8px;
    height: 8px;
    display: flex;
    margin-left: 10px;
    border-left: 2px solid #000;
    transform: rotate(-45deg);
    transition: 0.3s;
  }
  .top-info .burger-wrapper__burger {
    display: flex;
    width: 44px;
    height: 44px;
    background-color: #3157BE;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .top-info .burger-wrapper__burger span {
    margin: 2px;
    display: flex;
    width: 18px;
    height: 3px;
    background-color: #fff;
    border-radius: 20px;
  }
}
@media (max-width: 500px) {
  .top-info {
    justify-content: space-between;
    margin: 0 40px;
  }
  .top-info > .top-info__call {
    display: none;
  }
  .top-info .top-info__burger-wrapper {
    align-items: flex-end;
  }
}
nav {
  display: flex;
  justify-content: space-between;
  margin: 22px 0 20px 0;
  position: relative;
}
nav > * {
  border-radius: 20px;
  background-color: #EBEBEB;
  height: 51px;
  display: flex;
  align-items: center;
}
nav .menu {
  justify-content: space-evenly;
  width: 100%;
  max-width: 1013px;
}
nav .menu__item {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  cursor: pointer;
  border-radius: 100px;
  height: 100%;
  padding: 19px;
  transition: 0.3s;
}
nav .menu__item:hover {
  background-color: #3968E8;
  color: #fff;
}
nav .menu__item:hover span {
  border-color: #fff;
}
nav .menu__item > span {
  border-bottom: 2px solid #000;
  width: 8px;
  height: 8px;
  display: flex;
  margin-left: 10px;
  border-left: 2px solid #000;
  transform: rotate(-45deg);
  transition: 0.3s;
}
nav .menu__item-extended {
  width: 100%;
  display: none;
  top: 51px;
  left: 0;
  padding: 25px;
  background-color: #fff;
  border-radius: 20px;
  justify-content: space-between;
  color: #000;
  text-transform: none;
  font-size: 14px;
  cursor: initial;
}
nav .menu__item-col {
  display: flex;
  flex-direction: column;
  flex: 0 0 20%;
  max-width: 20%;
}
nav .menu__item-col > * {
  padding: 10px 20px;
}
nav .menu__item-col > a {
  color: #000;
  text-decoration: none;
  font-weight: normal;
  width: fit-content;
  border-radius: 20px;
  transition: 0.3s;
}
nav .menu__item-col > a:hover {
  background-color: #3968E8;
  color: #fff;
}
nav .menu__item:hover .menu__item-extended {
  display: flex;
  position: absolute;
  top: 51px;
}
nav .lang {
  width: 71px;
  justify-content: center;
  height: 51px;
  position: relative;
}
nav .lang .menu__item-extended {
  flex-direction: column;
  align-items: center;
  padding: 15px;
}
nav .lang .menu__item-extended a {
  margin: 5px 0;
  color: #000;
  text-decoration: none;
  font-weight: normal;
  width: fit-content;
  border-radius: 20px;
  transition: 0.3s;
}
nav .lang .menu__item-extended a:hover {
  color: #3968E8;
}
@media (max-width: 1025px) {
  nav {
    display: none;
  }
}
